

.form_title {
	color:  var(--hrm_palette-blue-purple);
	margin: 15px 7px 5px;
	font-weight: 600;
	font-size: 19px;
}


.cmp_dtls{
	padding: 15px 3px;
}


.cmp_desc{
	margin-top: 25px;
	padding: 15px 3px;
	border-top: 1px solid #dadce0;
	text-align: left;
}

.cmp_desc .description{
	margin-left: 12px;
}


.desc_lbl{
	margin-right: 8px;
}

.hdr_bar{
	padding: 10px 7px;
	border-radius: 8px;
}



.boxAddAcc{
	padding: 16px;
    border: 1px solid var(--color_text_dad);
    border-radius: 8px;
}



.icon svg{
	cursor: pointer;
}















.acc_dtls{
	padding: 0 11px 11px;
	margin-left: 30px;
}

.acc_dtls .code{
	margin-right: 12px;
	font-weight: 600;
	font-size: 18px;
	line-height: 18px;
}
.acc_dtls .name{
	font-weight: 500;
	line-height: 18px;
}


.comprobante_id{
	color:  var(--hrm_palette-blue-purple);
	font-weight: 600;
}


.item_acc  .icon {
	opacity: 0;
	
}

.item_acc:hover  .icon {
	opacity: 1;
	--opacity-btn_hover : opacity 350ms ease-in;
	transition: var(--opacity-btn_hover);
}

.header_tbl{
	padding: 10px 0;
}

.header_tbl._floating{
	min-width: 68%;
	padding-top: 10px;
}


.comprobante_id_bold{
	color:  var(--hrm_palette-blue-purple);
	font: 600 16px/24px var(--font_family);
	margin: 0 6px;
}








.filter_cmp_btn{
	padding: 6px;
	border:1px solid var(--filter_cmp_btn_color);
	border-radius: 7px;
}

.filter_cmp_btn:hover{
	background-color: var(--filter_cmp_btn_bck-color);
	cursor: pointer;
}

.filter_cmp_btn 
.lbl{
	font: 600 14px/24px var(--font_family);margin: 0 7px;
	color: var(--filter_cmp_btn_color);
}

.filter_cmp_btn 
svg{
	fill: var(--filter_cmp_btn_color);
}


.entryType{
	font: 600 14px/18px var(--font_family);
	color: var(--hrm_palette-blue-purple);
}



.msg_alert {
    padding: 6px 12px;
    border: 1px solid var( --hrm_palette-selected-nav-text);
    border-radius: 8px;
	margin: 12px auto;
	background-color: var( --hrm_palette-selected-nav-text-100);
	margin-left: 12px;
}

.msg_alert_title {
    font-family: Google Sans,Roboto,Arial,Helvetica,sans-serif;
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
    font-size: 14px;
    font-weight: 500;
	letter-spacing: .1px;
	color: var( --hrm_palette-selected-nav-text);
}

.lbl_saving{
	color: var(--hrm_palette-blue-purple);
	margin-right:5px;
	margin-top: 7px;
	font-family: "Google Sans","GoogleSans-Local";
	font-weight: 600;
}